@import url(https://fonts.googleapis.com/css2?family=Alegreya&family=Nanum+Myeongjo&display=swap);
body {
    background-color: rgba(134, 134, 134, 0.226);
    /* overflow-x: hidden; */
}
.Discography{
    width: 100%;
    padding: 0 10% 30px;
}
.Discography_wrap{
    padding: 10px;
}
.Discography_text_wrap{
    text-align: center;
    padding: 20px 0;
}
.Discography_text_wrap > h1 {
    display: inline-block;
    font-size: 3em;
    font-weight: bold;
    text-shadow: -1px 5px 4px rgba(150, 150, 150, 0.88);
    padding: 20px;
    border-radius: 15px;
}
.Discography_Album_wrap{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

@media (max-width: 905px){
    .Discography_Album_wrap {
        padding-left:  0;
        padding-right:  0;
    }
}
@media (max-width: 520px){
    .Discography_text_wrap{
        font-size: .7em;
    }
}
.Album_img > img{
    vertical-align: middle;
    width: 100%;
    height: 100%;
}
.Album_img {
    display: inline-block;
    width: 100%;
    height: 100%;
    padding: 3px;
    transition: all 2s ease;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transform: rotateY(0deg);
}
.Album:hover .Album_back_side_wrap{
    transform: rotateY(0deg);
    transition: .5s ease all;
}
.Album:hover .Album_img {
    transform: rotateY(180deg);
    transition: .5s ease all;
}
.Album_back_side_wrap{
    width: 100%;
    height: 100%;
    padding: 20px;
    background: white;
    z-index: 0;
    transform: rotateY(-180deg);
    transition: all 2s ease;
}
.Album_wrap{
    display: inline-block;
    width: 250px;
    height: 220px;
    position: relative;
}
.Album-back_side_release {
    padding: 2px;
    font-size: 1em;
}
.Album_name > figcaption {
    padding: 2px;
    font-weight: bold;
    font-size: 1em;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
    text-align: center;
}
.Album_back_side_name > h2, .Album_back_side_play_time > h3,.Album_back_side_release > h3{
    font-size: 1em;
}

.Album {
    box-shadow: 2px 6px 15px -3px #000000;
    margin: 15px;
}
@media (max-width: 1500px){
    .Album_wrap{
        width: 150px;
        height: 150px;
    }
    .Album_back_side_wrap{
        padding: 10px;
    }
    .Album_back_side_name,.Album_back_side_play_time,.Album_back_side_release{
        font-size: .8em;
    }
}

.Modal{
    width: 100vw;
    height: 100vh;
    background-color: rgba(53, 53, 53, 0.466);
    padding: 100px 300px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
}
.Modal_wrap{
    width: 100%;
    height: 100%;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.623);
    position: relative;
}
.Modal_Exit_Button{
    position: absolute;
    top: -1px;
    right: 0;
    border-radius: 5px;
}
.Modal_Exit_Button > i {
    font-size: 1.7em;
}
.Modal_Exit_Button:active {
    transform: scale(1.1,1.1);
    opacity: 0.5;
}

@media (max-width:1535px) {
    .Modal {
        padding: 50px 200px;
    }
}
@media (max-width:1350px) {
    .Modal {
        padding: 50px 100px;
    }
}
@media (max-width:1150px) {
    .Modal {
        padding: 50px 50px;
    }
}
.Modal_audio_wrap{
    padding: 20px;
    width: 30%;
    height: 100%;
    float: left;
}
.Modal_audio_play_wrap > audio{
    width: 100%;
    height: 100%;
}
.Modal_audio_play_wrap {
    height: 10%;
}
.Modal_audio_play_wrap > audio{
    height: 10%;
    width: 100%;
    display: block;
}
.Modal_audio_image_play_wrap{
    height: 35%;
    padding: 30px;
}
@media (max-width:950px) {
    .Modal_audio_wrap{
        width: 50%;
    }    
}
@media (max-width:620px) {
    .Modal_audio_wrap{
        height: 30%;
        float: none;
        width: 100%;
        padding: 10px 27px;
        text-align: center;
    }
    .Modal_audio_image_play_wrap{
        padding: 0;
    }
}
@media (max-width:350px) {
    .Modal_audio_wrap {
        padding: 1% 5%;
    }
}
.Modal_audio_image_wrap > span > img{
    vertical-align: middle;
    width: 100%;
    height: 100%;
}
.Modal_audio_image_wrap > span {
    display: inline-block;
    height: 100%;
}
.Modal_audio_image_wrap {
    height: 90%;
    text-align: center;
}
@media (max-width:620px) {
    .Modal_audio_image_play_wrap{
        height: 90%;
    }
}
@media (max-width:350px) {
    .Modal_audio_image_play_wrap{
        padding: 0;
    }
}
.Modal_audio_play_wrap > audio{
    width: 100%;
    height: 100%;
}
.Modal_audio_play_wrap {
    height: 10%;
    width: 100%;
    display: inline-block;
}
.Modal_audio_Explanation{
    height: 65%;
    padding: 10px;
    border: 5px solid black;
}
.Modal_audio_Explanation_wrap{
    height: 100%;
    overflow-y: scroll;
}
.Modal_audio_Explanation_wrap::-webkit-scrollbar {
    display: none;
}
@media (max-width:620px) {
    .Modal_audio_Explanation{
        display: none;
    }   
}
.Modal_audio_Explanation_title_wrap {
    text-align: center;
}
.Modal_audio_Explanation_title > span{
    font-size: 1.5em;
    font-weight: bold;
}
.Modal_audio_Explanation_title{
    padding: 10px;
}
.Modal_audio_Explanation_Description_wrap{
    text-align: center;
}
.music{
    width: 100%;
    background-color: white;
    height: 25px;
    line-height: 25px;
    font-size: 1em;
    border-radius: 10px;
    margin-top: 10px;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
    text-align: center;
}
.music:last-child {
    margin-bottom: 5px;
}
.active {
    background-color: black;
    color: white;
    font-weight: bold;
}
@media (max-width:620px) {
    .music{
        font-size: .8em;
    }
}
.Modal_Music_List{
    padding: 25px;
    width: 70%;
    height: 100%;
    float: left;
}
.Modal_Music_List_wrap{
    width: 100%;
    height: 100%;
    list-style-type: none;
    overflow-y: scroll;
}
.Modal_Music_List_wrap::-webkit-scrollbar {
    display: none;
}
@media (max-width:950px) {
    .Modal_Music_List{
        width: 50%;
    }    
}
@media (max-width:620px) {
    .Modal_Music_List{
        height: 70%;
        width: 100%;
        float: none;
        padding: 0 10px;
    }
}
.Profile_List{
    width: 100%;
    padding: 0 10%;
    overflow-x: hidden;
}
@media (max-width:800px) { 
    .Profile_List{
        padding: 0 5%;
    }
}
.Profile_text_wrap{
    text-align: center;
    padding: 20px 0;
}
.Profile_text_wrap > span{
    font-weight: bold;
    font-size: 3em;
    padding: 20px;
    display: inline-block;
    text-shadow: -1px 5px 4px rgba(150, 150, 150, 0.88);
    color: #242424;
}
.Profile_image > img {
    width: 100%;
    height: 100%;
    vertical-align: middle;
}
.Profile_image_wrap{
    display: block;
    height: 500px;
    width: 30%;
    border: 10px solid white;
}
.Profile_wrap:nth-child(odd) {
    box-shadow: 7px 13px 15px 2px #000000;
}
.Profile_wrap:nth-child(even){
    box-shadow: -11px 13px 15px 2px #000000;
}
.Profile_wrap:nth-child(odd) .Profile_image_wrap{
    float: left;
}
.Profile_wrap:nth-child(even) .Profile_image_wrap{
    float: right;
}
.Profile_explain_wrap{
    float: left;
    padding: 10px;
    width: 70%;
    height: 500px;
}
.Profile_name{
    font-size: 3em;
    text-transform: capitalize;
    font-weight: bold;
}
.Profile_explain_text{
    padding: 10px 0;
}
.Profile_explain_text_wrap::-webkit-scrollbar {
    display: none; 
}
.Profile_name_wrap{
    height: 15%;
}
.Profile_explain_text_wrap{
    height: 75%;
    overflow-y: scroll;
}
.Profile_wrap{
    background-color: rgb(128, 126, 126);
    margin: 25px 0;
    border-radius: 5px;
}
.Profile_wrap:after {
    content: "";
	display: block;
	clear: both;
}
.Profile_birth_wrap{
    height: 10%;
}
@media (max-width:800px) {
    .Profile_wrap:nth-child(odd) .Profile_image_wrap{
        float: none;
    }
    .Profile_wrap:nth-child(even) .Profile_image_wrap{
        float: none;
    }
    .Profile_image_wrap,.Profile_explain_wrap{
        width: 100%;
    }
    .Profile_name_wrap{
        font-size: .8em;
        padding: 15px 0;
    }
    .Profile_birth_wrap{
        font-size: .9em;
        padding: 10px 0;
    }

}
.Navigation{
    width: 100%;
    padding: 0 10%;
    color: white;
    background-color: #0000000c;
    position: relative;
}
.Navigation_wrap{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    list-style: none;
    position: relative;
}
.Navigation_wrap > li {
    padding: 10px 0;
    font-size: 1.3em;
}
header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 998;
}
.bookmark {
    text-decoration: none;
    color: white;
    vertical-align: middle;
}
.menu_toggle{
    position: absolute;
    right: 10px;
}
.menu_toggle > button{
    width: 50px;
    height: 50px;
    border: none;
    outline: none;
    background-color: rgba(0, 0, 0, 0);
}
.menu_toggle > button > i{
    font-size: 2em;
    color: white;
}
.Menu{
    width: 100%;
    display: flex;
    flex-direction: column;
    list-style: none;
    position: absolute;
    z-index: 997;
}
.Menu > li {
    background-color: rgb(59, 54, 54);
    padding: 10px;
}
.menu_bookmark {
    text-decoration: none;
    color: white;
}
@media (min-width: 800px) {
    .menu_toggle {
        display: none;
    }
    .Menu {
        display: none;
    }
}
@media (max-width: 800px) {
    .bookmark {
        display: none;
    }
    .Navigation{
        padding: 0;
    }
}
.logo_bar_img {
    vertical-align: middle;
    width: 100%;
    height: 100%;
}
.logo_bar_wrap {
    display: inline-block;
    transition: all .5s ease;
}
.logo_bar {
    width: 100%;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Nanum Myeongjo','Alegreya';
}
html {
    scroll-behavior: smooth;
}

